body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f7f9fc;
}

.homepage {
    text-align: center;
    padding: 50px;
    color: #333;
}

.header h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    color: #2c3e50;
}

.header p {
    font-size: 1.2rem;
    margin-bottom: 40px;
}

.actions .btn {
    margin: 10px;
    padding: 15px 25px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btn-primary {
    background-color: #3498db;
    color: white;
}

.btn-secondary {
    background-color: #2ecc71;
    color: white;
}

.actions .btn:hover {
    opacity: 0.8;
}
