.dashboard {
    text-align: center;
    padding: 50px;
    background-color: #f7f9fc;
    min-height: 100vh;
}

.dashboard h1 {
    font-size: 2.5rem;
    color: #2c3e50;
    margin-bottom: 20px;
}

.dashboard p {
    font-size: 1.2rem;
    margin-bottom: 40px;
    color: #666;
}

.actions .btn {
    margin: 10px;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btn-primary {
    background-color: #3498db;
    color: white;
}

.btn-secondary {
    background-color: #2ecc71;
    color: white;
}

.actions .btn:hover {
    opacity: 0.8;
}
